<template>
  <component
    :key="item"
    :is="item"
    v-for="item of names"></component>
</template>

<script>
const components = {}
// 需打开vue.config.js的:同时使用方式一和方式二 处理md文件 https://github.com/wxsms/vue-md-loader
// const requireModule = require.context('./components', true, /.md$/);
// 只使用方式一 处理md文件
// const requireModule = require.context('./components/one', true, /.md$/);
// 需打开vue.config.js的:同时使用方式一和方式二 处理md文件 https://github.com/wxsms/vue-md-loader
// const requireModule = require.context('./components/two', true, /.md$/);
// 渲染md里的vue代码
const requireModule = require.context('./components/three', true, /.md$/);

requireModule.keys().forEach(fileName => {
  // defineAsyncComponent(() => import(`./md/${fileName.slice(2)}`))
  components[fileName.slice(2, -3)] = requireModule(fileName).default;
});
console.log(components)
// import T from './md/t.md';

export default {
  components,
  setup() {
    return {
      names: Object.keys(components).map(item => item)
    }
  }
}
</script>
